
import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles, } from '@material-ui/core/styles';
import {
    Container, MenuItem, List, ListItem, ListItemText,
    IconButton, ListItemSecondaryAction
} from "@material-ui/core";
import * as Yup from 'yup'
import { Minus, Plus } from 'mdi-material-ui';
import Input from '../../../ui-component/shared/Input';
import { Formik, Form, ErrorMessage } from 'formik'

import {
    Grid, Paper, TextField,
    Typography
} from '@material-ui/core';
import { Autocomplete } from "@material-ui/lab"
import { MoonLoader } from 'react-spinners'; // Importe o componente BounceLoader

import Swal from 'sweetalert2'

import { requesicoes } from "../../../utils/services";


const useStyles = makeStyles((theme) => ({
    root: {
        '&:hover': {
            backgroundColor: 'transparent',
        },
    },

}));

let cursos = [];
let cadeiras = [];
let cadeirasIndisponiveis = [];
let cadeirasCurriculo = [];
let anos = [];
let semestres = [];
export default function NovoCurriculo() {

    const classes = useStyles();

    const paperStyle = { padding: '50px 25px', width: 750, margin: ' auto' }

    const [to, setTo] = useState('');
    const [redirect, setRedirect] = useState(false);
    const [searchTerm, setSearchTerm] = useState('');
    const [carregando, setCarregando] = useState(true);
    const [updateDom, setUpdateDom] = useState(false);
    

    useEffect(() => {
        cursos = [];
        cadeiras = [];
        init();
        setTimeout(() => {
            setCarregando(false);
        }, 2000);
    }, [])

    async function init() {
        const [cursosRequest, anoRequest, semestreRequest, cadeirasRequest] = await Promise.all([requesicoes().buscarCurso(),//'?campos=id,nomeCurso,cadeiras[id,nome]'
        requesicoes().buscarAnosFreque(), requesicoes().buscarSemestre(), requesicoes().buscarCadeiras()]);
        cursos = cursosRequest.data;
        anos = anoRequest.data;
        semestres = semestreRequest.data;
        cadeiras = cadeirasRequest.data;
        setUpdateDom(!updateDom);
    }

    async function getCadeiras(id) {
        cadeiras = cursos.filter(x => x.id == id)[0].cadeiras;
        // console.log(cadeiras);
        setUpdateDom(!updateDom);
    }

    return (
        <Container className="AppNovoEstudante">
            {carregando ? (
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
                    <MoonLoader color="#36d7b7" />
                </div>

            ) : (
                <Paper elevation={5} style={paperStyle} >
                    <Grid align='left'>
                        <Typography variant='h4'>Novo Curriculo</Typography>
                        <Typography variant='caption'>Prencha os dados para cadastrar um novo curriculo</Typography>
                    </Grid>
                    <br></br>

                    <Formik
                        initialValues={{}}
                        onSubmit={(values, { setSubmitting }) => {


                            async function save(values) {
                                try {
                                    values.semestres = cadeirasCurriculo.map(x => ({ ...x, cadeiras: x.cadeiras.map(y => cadeiras.filter(z => z.id == y)[0]) }));
                                    console.log('====Dados: ',JSON.stringify(values));

                                    await Promise.resolve(requesicoes().postCurricula([values]));
                                    Swal.fire(
                                        'Sucess!',
                                        'Curriculo cadastrado com sucesso!',
                                        'success'
                                    )

                                } catch (error) {
                                    Swal.fire({
                                        icon: 'error',
                                        title: 'Oops...',
                                        text: 'Ocorreu um erro ao cadastrar Curriculo!!',
                                        footer: 'Se o erro pressistir porfavor contacte o centro Informatico'
                                    })



                                }
                            }
                            save(values);
                            setTimeout(() => {
                                setSubmitting(false);
                            }, 500);
                        }}

                        validationSchema={Yup.object().shape({
                            ano: Yup.string().min(4, "Ano Incorreto").required("Campo Obrigatorio"),
                        })}>
                        {props => {
                            const {
                                values, setFieldValue
                            } = props;
                            return (<Form>
                                <Grid container spacing={2}>
                                    <Grid item sm={6}>

                                    </Grid>
                                    <Grid item sm={6} style={{ textAlign: 'right' }}>
                                        <Button disabled={!Boolean(values.curso)} size="small" onClick={() => {
                                            cadeirasCurriculo.push({ cadeiras: [] });
                                            setUpdateDom(!updateDom);
                                        }} color="primary" variant="contained">Adicionar Semestre</Button>
                                    </Grid>
                                    <Grid item sm={6}>
                                        <Autocomplete onChange={(e, v) => {
                                            setFieldValue('curso', v);
                                            //getCadeiras(v.id);
                                        }}
                                            fullWidth
                                            getOptionLabel={option => option.nome}
                                            options={cursos} style={{ minWidth: 200 }}
                                            renderInput={(params) => <TextField variant="outlined" margin="dense" label="Curso"
                                                fullWidth {...params} placeholder="Seleccione uma opção" />}
                                        />
                                    </Grid>
                                    <Grid item sm={6}>
                                        <Input name="ano"
                                            label="Ano"
                                            placeholder="Digite Aqui..."
                                            helperText={<ErrorMessage name='ano' />}
                                            error={props.errors.ano && props.touched.ano} />
                                    </Grid>
                                    {cadeirasCurriculo.map((c, i) => <Grid key={i} item sm={12}><Cadeira index={i} /></Grid>)}

                                    <Grid item sm={12} style={{ paddingTop: '10px', textAlign: 'right' }}>
                                        <Button color="secondary">Cancelar</Button> &nbsp;&nbsp;&nbsp;
                                        <Button type="submit"
                                            color="primary" variant="contained">Salvar</Button>
                                    </Grid>

                                </Grid>
                            </Form>
                            )
                        }}</Formik>
                </Paper>
            )}
        </Container >
    );
}
function Cadeira(props) {

    const [updateDom, setUpdateDom] = useState(false);

    const move = {
        adicionarCadeira: value => {
            cadeirasCurriculo[props.index].cadeiras.push(value);
            cadeirasIndisponiveis.push(value);
            setUpdateDom(!updateDom);
        },
        removerCadeira: value => {
            cadeirasIndisponiveis = cadeirasIndisponiveis.filter(x => x !== value);
            cadeirasCurriculo[props.index].cadeiras = cadeirasCurriculo[props.index].cadeiras.filter(x => x !== value);
            setUpdateDom(!updateDom);
        }
    }
    const [searchTerm, setSearchTerm] = useState('');

    const customList = (items, name) => {

        const filteredItems = items.filter(value =>
            value.nome.toLowerCase().includes(searchTerm.toLowerCase())
        );

        return (
            <Paper elevation={0} style={{ border: '1px solid #c4c4c4', borderRadius: '5px', width: '100%', height: 280, overflow: 'auto' }} square>
                <TextField
                    label="Buscar"
                    variant="outlined"
                    margin="dense"
                    fullWidth
                    value={searchTerm}
                    onChange={e => setSearchTerm(e.target.value)}
                />
                <List dense component="div" role="list">
                    {filteredItems.map((value, index) =>
                        <ListItem onDoubleClick={() => move[name](value.id)}
                            key={index} role="listitem" button>
                            <ListItemText primary={value.nome} />
                            <ListItemSecondaryAction>
                                <IconButton color={name.includes('remover') ? 'secondary' : 'primary'} size="small"
                                    onClick={() => {
                                        move[name](value.id);
                                    }}>
                                    {name.includes('remover') ? <Minus /> : <Plus />}
                                </IconButton>
                            </ListItemSecondaryAction>
                        </ListItem>)}
                    <ListItem />
                </List>
            </Paper>
        );
    }


    return (<Paper elevation={2} style={{ padding: '15px' }}>
        <Grid container spacing={2}>
            <Grid item sm={6}>
                <TextField select variant="outlined" margin="dense" name="anoFrequencia"
                    onChange={(e) => { cadeirasCurriculo[props.index].anoFrequencia = e.target.value; }}
                    placeholder="Seleccione o ano" label="Ano" fullWidth type="text">
                    {anos.map(opt => <MenuItem key={opt} value={opt.values}>{opt.label}</MenuItem>)}
                </TextField>
            </Grid>
            <Grid item sm={6}>
                <TextField select variant="outlined" margin="dense" name="semestre"
                    onChange={(e) => { cadeirasCurriculo[props.index].semestre = e.target.value; }}
                    placeholder="Seleccione o semestre" label="Semestre" fullWidth type="text">
                    {semestres.map(opt => <MenuItem key={opt} value={opt}>{opt}</MenuItem>)}
                </TextField>
            </Grid>

            <Grid item sm={6}>
                {customList(cadeiras.filter(y => !cadeirasCurriculo[props.index].cadeiras.includes(y.id) && !cadeirasIndisponiveis.includes(y.id)), 'adicionarCadeira')}
            </Grid>

            <Grid item sm={6}>
                {customList(cadeiras.filter(y => cadeirasCurriculo[props.index].cadeiras.includes(y.id)), 'removerCadeira')}
            </Grid>

        </Grid>
    </Paper>)
}